* {
  box-sizing: border-box;
}

body {
  text-align: center;
  background: url(../images/trolltunga.jpeg);
  background-size: cover;
  background-position: 50%;
  height: 100vh;
  width: 100vw;
  margin: 0;
  padding: 0;
  cursor: crosshair;
}

svg {
  position: absolute;
  top: 24.5%;
  margin-top: -60px;
  left: 48%;
  margin-left: -325px;
}

.navbar {
  display: flex;
  position: relative;
  justify-content: center;
  align-items: center;
  background-color: transparent;
}

.navbar .active {
  color: #005b57;
}

.navbar-links {
  height: 100%;
}

.navbar-links ul {
  display: flex;
  margin: 0;
  padding: 0;
}

.navbar-links li {
  list-style: none;
}

.navbar-links li a {
  display: block;
  text-decoration: none;
  color: whitesmoke;
  padding: 1rem;
}

.navbar-links li:hover {
  color: #555;
}

.toggle-button {
  position: absolute;
  top: 0.75rem;
  right: 1rem;
  display: none;
  flex-direction: column;
  justify-content: space-between;
  width: 30px;
  height: 21px;
}

.toggle-button .bar {
  height: 3px;
  width: 100%;
  background-color: white;
  border-radius: 10px;
}

@media (max-width: 800px) {
  .navbar {
    flex-direction: column;
    align-items: flex-start;
  }

  .toggle-button {
    display: flex;
  }

  .navbar-links {
    display: none;
    width: 100%;
  }

  .navbar-links ul {
    width: 100%;
    flex-direction: column;
  }

  .navbar-links ul li {
    text-align: center;
  }

  .navbar-links ul li a {
    padding: 0.5rem 1rem;
  }

  .navbar-links.active {
    display: flex;
  }
}
